.preorder-view {
    margin: 10px;

    & > .toolbar {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        & > .btn-group {
              margin: 4px;
        }
    }

    & > .media {
        display: flex;
        flex-direction: column;
        overflow: scroll;

        & > .media-row {
            display: flex;
            flex-direction: row;
            align-items: center;

            & > .title {
                width: 100px;
            }

            & > .gallery {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;

                & > .image-container {
                    padding: 8px;
                }
            }
        }
    }
}

@media only screen and (max-width: 700px) {
  .preorder-view {
      & > .toolbar {
          justify-content: center;
          flex-wrap: wrap;
      }
    }
}
